<template>
  <div class="edit-info">
    <h2 class="title">
      {{$route.name == '修改小说链接' ? '修改小说链接' : '创建小说链接'}}
    </h2>
    <Form :model="form" class="form-group" label-position="right" :label-width="100">
      <FormItem label="类型" prop="type">
        <RadioGroup v-model="form.type">
          <Radio :label="0">微跳</Radio>
          <Radio :label="1">非微调单页</Radio>
          <Radio :label="2">非微调多页</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="id" prop="id">
        <Input :disabled="$route.name == '修改小说链接'" size="large" type="text" v-model="form.id" placeholder="请输入id"></Input>
      </FormItem>
      <FormItem label="页面标题" prop="pageTitle">
        <Input size="large" type="text" v-model="form.pageTitle" placeholder="请输入页面标题"></Input>
      </FormItem>
      <FormItem label="文章标题" prop="articleTit">
        <Input size="large" type="text" v-model="form.articleTit" placeholder="请输入文章标题"></Input>
      </FormItem>
      <FormItem label="公号名称" prop="name">
        <Input size="large" type="text" v-model="form.name" placeholder="请输入公号名称"></Input>
      </FormItem>
      <FormItem label="公号描述" prop="title">
        <Input size="large" type="text" v-model="form.desc" placeholder="请输入公号描述"></Input>
      </FormItem>
      <FormItem label="图片" prop="picurl">
        <Upload action="http://n.jtjsmp.top/tuiguang/novel/upload" :show-upload-list="false" name="imageFile" :on-success="handleSuccess">
          <Button icon="ios-cloud-upload-outline">点击上传</Button>
          <br />
          <img v-if="imgUrl" :src="imgUrl" alt="" width="200" style="margin-top:10px;">
        </Upload>
      </FormItem>
      <FormItem label="文章内容" prop="content">
        <Input size="large" type="textarea" v-model="form.capter1" placeholder="请输入文章内容"></Input>
      </FormItem>
      <FormItem v-if="form.type == 2" label="文章内容(第二章)" prop="content">
        <Input size="large" type="textarea" v-model="form.capter2" placeholder="请输入文章内容(第二章)"></Input>
      </FormItem>
      <FormItem v-if="form.type == 0" label="跳转链接" prop="linkUrl">
        <Input size="large" type="text" v-model="form.linkUrl" placeholder="请输入跳转链接"></Input>
      </FormItem>
      <FormItem label="统计链接">
        <Input size="large" type="text" v-model="form.statisticsUrl1" placeholder="请输入统计链接"></Input>
      </FormItem>
      <FormItem v-if="form.type == 2" label="统计链接(第二章)">
        <Input size="large" type="text" v-model="form.statisticsUrl2" placeholder="请输入统计链接(第二章)"></Input>
      </FormItem>
      <FormItem label="其他业务代码">
        <Input size="large" type="text" v-model="form.tokenCodes" placeholder="请输入代码"></Input>
      </FormItem>
      <FormItem label="渠道">
        <Input size="large" type="text" v-model="form.channel" placeholder="请输入渠道"></Input>
      </FormItem>
      <FormItem label="备注">
        <Input size="large" type="text" v-model="form.remarks" placeholder="请输入备注"></Input>
      </FormItem>
      <FormItem>
          <Button v-if="$route.name == '创建小说链接'" class="edit-btn" size="large" type="primary" @click="create">立即创建</Button>
          <Button v-else class="edit-btn" size="large" type="primary" @click="save">保 存</Button>
          <Button class="edit-btn" size="large" type="warning" @click="reset">重 置</Button>
          <Button class="edit-btn" size="large" type="error" @click="cancel">取 消</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        type: 0,
        pageTitle: "冒死推荐，多看一本是一本",
        articleTit: " 女人最多能承受多少厘米",
        desc: "一个陪你过夜的小说站"
      },
      imgUrl: "",
      domain_names: []
    };
  },
  mounted() {
    this.getDomain();
    if (this.$route.name == "修改小说链接") {
      this.form = this.$route.params.row;
      this.imgUrl = this.$route.params.domain_name + this.form.picurl;
      this.getContent(this.form._id);
    }
  },
  methods: {
    getContent(id) {
      this.$axios
        .get("/tuiguang/novel/get_content", { params: { _id: id } })
        .then(res => {
          this.$set(this.form, "capter1", res.data.data.capter1);
          this.$set(this.form, "capter2", res.data.data.capter2);
        });
    },
    handleSuccess(res, file) {
      this.imgUrl =
        this.domain_names[0].domain_name + "/images/tuiguang/" + res.filename;
      this.form.picurl = "/images/tuiguang/" + res.filename;
    },
    create() {
      this.$axios.post("/tuiguang/novel/add", this.form).then(res => {
        if (res.data.err) {
          this.$Message.info(res.data.err);
        } else {
          this.$Message.info(res.data.message);
          this.$router.push({
            name: "小说链接",
            params: this.form
          });
          this.reset();
        }
      });
    },
    reset() {
      this.form = {
        type: 0,
        pageTitle: "冒死推荐，多看一本是一本",
        articleTit: " 女人最多能承受多少厘米",
        desc: "一个陪你过夜的小说站"
      };
      this.imgUrl = "";
    },
    cancel() {
      this.reset();
      this.$router.push({
        name: "小说链接",
        params: { state: "cancel" }
      });
    },
    save() {
      this.$axios
        .post("/tuiguang/novel/update", {
          _id: this.form._id,
          type: this.form.type,
          id: this.form.id,
          pageTitle: this.form.pageTitle,
          articleTit: this.form.articleTit,
          name: this.form.name,
          desc: this.form.desc,
          picurl: this.form.picurl,
          capter1: this.form.capter1,
          capter2: this.form.capter2,
          linkUrl: this.form.linkUrl,
          statisticsUrl1: this.form.statisticsUrl1,
          statisticsUrl2: this.form.statisticsUrl2,
          tokenCodes: this.form.tokenCodes,
          channel: this.form.channel,
          remarks: this.form.remarks
        })
        .then(res => {
          this.$Message.info(res.data.success);
          this.$router.push({
            name: "小说链接",
            params: {
              row: this.form,
              index: this.$route.params.index
            }
          });
          this.reset();
        });
    },
    getDomain() {
      this.$axios.get("/tuiguang/novel/show").then(res => {
        this.domain_names = res.data.domain_names;
      });
    }
  }
};
</script>

<style scoped>
.edit-info {
  width: 500px;
  margin: 0 auto;
  text-align: left;
}
.title {
  font-size: 36px;
  margin-bottom: 30px;
  text-align: center;
}

.edit-btn {
  margin-right: 10px;
}
</style>
